/* ////////////////  MERCHANT LAYOUT /////////////// */
.merchantLayoutContainer {
  margin: 0;
  padding: 0;
  width: 100vw;
  display: flex;
  flex-direction: column;
}
.merchantOutletWrapper {
  margin: 0;
  padding: 0;
  /* display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 0; */
  display: grid;
    grid-template-columns: 20vw 80vw;
}
.merchantOutletContainer {
  margin: 0;
  padding: 0;
  /* margin-left: 20vw; */
  /* margin-top: 16vh; */
  background-color: #F5F5FE;
  margin-left: -36px;
}

/* ////////////////// MERCHANT FOOTER //////////////////////// */
.merchantFooterWrapper {
  margin: 0;
  padding: 0;
  width: 80vw;
  margin-left: 20vw;
  overflow: hidden;
}
.merchantFooterWrapper .merchantFooterInner {
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 24px;
  width: 100%;
  height: 45px;
  background-color: rgb(10, 106, 187);
}
.merchantFooterWrapper .merchantFooterRow {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 12px;
}
.merchantFooterWrapper .merchantFooterText {
  color: white;
  font-family: "Poppins";
  font-size: 14px;
  font-weight: 400;
}
.merchantFooterWrapper .footerRowDivider {
  width: 1px;
  height: 18px;
  background-color: rgb(166, 166, 166);
}
.merchantFooterWrapper .merchantChildFooter {
  margin: 0;
  padding: 0;
  height: 60px;
  font-family: "Poppins";
  font-size: 14px;
  font-weight: 400;
  color: white;
  text-align: center;
  align-content: center;
  background-color: rgb(30, 86, 169);
}

/* ///////////////    RESUABLE COMPONENTS    ////////////// */
.button_large {
  margin: 0;
  padding: 18px 36px;
  background-color: rgb(253, 218, 102);
  color: #000000;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  max-width: 320px;
  max-height: 66px;
}
.large_button_text {
  margin: 0;
  padding: 0;
  color: #000000;
  font-size: 20px;
  font-family: "Poppins";
  font-weight: 700;
  letter-spacing: 0.6px;
}

/* //////////////////  MERCHANT LAYOUT HEADER  ///////////////// */
.onboardingPageHeaderWrapper {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 110px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999999999999;
  background-color: #ffffff;
}
.onboardingPageChildHeaderContainer {
  margin: 0;
  height: 42px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  background: rgb(21, 111, 172);
}
.childHeaderText {
  margin: 0;
  padding: 0;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  font-family: "Poppins";
  vertical-align: middle;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.onboardingPageHeaderMainContainer {
  margin: 0;
  padding: 12px 4%;
  width: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: #ffffff;
}
.headerLeft {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 4px;
}
.locationIcon {
  width: 60px;
  height: 60px;
  object-fit: contain;
}
.headerWebsiteTitleLight {
  margin: 0;
  padding: 0;
  font-size: 26px;
  color: rgb(4, 48, 121);
}
.headerWebsiteTitleDark {
  margin: 0;
  margin-top: -6px;
  padding: 0;
  font-size: 24px;
  color: rgb(76, 139, 198);
  font-weight: 700;
}
.headerRight {
  margin: 0;
  padding: 0 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 52px;
  position: relative;
}
.li_item {
  margin: 0;
  padding: 0;
  color: #000000;
  background-color: white;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-family: "Josefin Sans";
  user-select: none;
  -moz-user-select: none;
}
.li_item_active {
  color: #156fac;
}
.li_item:hover {
  transition: 150ms ease;
  cursor: pointer;
  color: #156fac;
}

.header_Btn {
  margin: 0;
  padding: 12px;
  width: 140px;
  color: #ffffff;
  font-size: 17px;
  font-weight: 700;
  background: #106cb5;
  border: none;
  border-radius: 50px;
  letter-spacing: 1px;
}
.headerRightMobileView {
  display: none;
}
.headerDropdownContainer {
  margin: 0;
  padding: 12px 0 6px 0;
  width: 120px;
  background-color: white;
  border: none;
  border-radius: 4px;
  position: absolute;
  top: 35px;
  right: 210px;
  box-shadow: 0 0 10px 0 gray;
}
.dropdownItem {
  margin: 0;
  padding: 2px 8px;
  color: black;
  background-color: white;
  font-size: 14px;
  cursor: pointer;
}
.dropdownItem:hover {
  color: white;
  background-color: #3490e0;
}

.mobileViewMenuModalWrapper {
  display: none;
}

