  padding: 10px;
  min-width: 240px;
  max-width: 520px;
  height: 52px;
  max-height: 52px;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: 10px;
  border: none;
  border-radius: 6px;
  position: absolute;
  overflow: hidden;
  z-index: 99999999999999;
  transition: 100ms ease;
}
.toastLeft {
  margin: 0;
  padding: 0 0 0 4px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 10px;
}
.toastTitle {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Poppins";
  font-size: 16px;
  font-weight: 500;
}
.toastMessage {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Poppins";
  font-size: 14px;
  font-weight: 400;
}
.toastCloseBtn {
  margin: 0;
  padding: 0;
  margin-top: -8px;
  color: #ffffff;
  font-family: "Poppins";
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}

